<template>
  <main class="main">
    <div class="container">
      <h1 class="main-title">Users</h1>
      <div class="main__box">
        <div class="main__box-item" v-for="user in allUsers" :key="user.id">
          <h3>{{ user.name }}</h3>
          <p>{{ user.username }}</p>
          <p>{{ user.phone }}</p>
          <p>{{ user.email }}</p>
          <p>{{ user.address.city }}</p>
          <router-link :to="{name:'usersId', params:{id:user.id}}" class="main__box-btn">Подробнее</router-link>
        </div>
      </div>
    </div>
  </main>
</template>


<script>
import { mapActions, mapState } from "vuex";
export default {
  methods: {
    ...mapActions(["getUsers"]),
  },
  computed: {
    ...mapState(["allUsers"]),
  },
  created() {
    this.getUsers();
  },
};
</script>

